  position: relative;
  width: 90%;
  max-width: 1200px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  overflow-x: hidden;
  height: 300px;
}

.segment-area {
  background: linear-gradient(135deg, #F7F9FC 0%, #E5E7EB 100%);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  width: 100%;
  height: 90%;
}

.carousel-wrapper {
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.carousel-wrapper::-webkit-scrollbar {
  display: none;
}


.carousel-card {
  display: inline-block;
  padding: 20px;
  width: 240px;
}

.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
