___CSS_LOADER_EXPORT___.push([module.id, `.BCCardFlipped .BCBook-card {
    transform: rotateY(180deg);
}

.BCBook-card {
    width: 120%;
    height: 190%;
    position: relative;
    transform-style: preserve-3d;
    transform: rotateY(0);
    transition: transform 0.5s;
}

.BCCard-face {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border: 1px solid #252525;
    border-radius: 0.5rem;
    overflow: hidden;
}

.BCCard-front {
    background: #fbfbf8;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px rgba(255, 255, 255, 0.4);
}

.BCCard-front:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.BCCard-back {
    transform: rotateY(180deg);
    background: #fbfbf8;
    text-align: center;
    padding: 10px;
    overflow-y: auto;
    white-space: normal;
}

.BCCard-back::-webkit-scrollbar {
    display: none;
}

.BCCard-back {
    scrollbar-width: none;
}


.BCText-container {
    width: 100%;
    height: 90px;
    overflow: hidden;
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, rgba(128,128,128,1) 100%);
}


.BCText-container p {
    padding: 15px 5px 15px 5px;
    position: absolute;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media (max-width: 68px) {
    .BCCard-wrapper {
        min-width: 50px;
    }
    .BCAuthor-card {
        width: 100%;
    }
}


.BCCard-wrapper {
    perspective: 1000px;
    width: 130px;
    height: 120px;
    padding: 0px 0px 0px 0px;
}
  
.BCImage-container {
height: calc(100% - 20px);
width: 100%;
border-bottom: 1.12px solid #252525;
}

.BCAuthor-name {
font-family: 'Nunito';
font-weight: 700;
white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
}
  

.BCStreamed-description {
font-family: 'Times New Roman', cursive;
font-size: 1rem;
font-weight: 400;
line-height: 0.96rem;
}


.BCReadBook-button {
    background-color: #373737;
    color: white;
    padding: 10px 30px;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    transition: background-color 0.3s, transform 0.3s;
    font-size: 0.875rem;
    white-space: nowrap;
}

.BCReadBook-button:hover {
    background-color: #252984;
    transform: translateX(-50%) scale(1.05);
}
